import React from "react"
import LightboxReact from 'react-image-lightbox';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import 'react-image-lightbox/style.css';

const Lightbox = ({
    images,
    selectedImage,
    handleClose,
    handlePrevRequest,
    handleNextRequest,
    className
}) => {
    const array = []
    images.forEach(image => {
        array.push(getImage(image))
    })
    const prevIndex = (selectedImage + array.length - 1) % array.length
    const nextIndex = (selectedImage + array.length + 1) % array.length

    const mainSrc = array[selectedImage]?.images?.fallback?.src
    const nextSrc = array[nextIndex]?.images?.fallback?.src
    const prevSrc = array[prevIndex]?.images?.fallback?.src
    return (
        <LightboxReact
            enableZoom={false}
            clickOutsideToClose={true}
            mainSrc={mainSrc || ''}
            nextSrc={nextSrc || ''}
            prevSrc={prevSrc || ''}
            onCloseRequest={handleClose}
            onMovePrevRequest={handlePrevRequest(selectedImage, array.length)}
            onMoveNextRequest={handleNextRequest(selectedImage, array.length)}
        />
    )
}

export default Lightbox